@media (min-width:1400px) {
    .account-wrapper {
        grid-template-columns: 363px calc(100% - 363px - 23px - 23px)
    }
}

@media (min-width:1413px) {
    .account-wrapper {
        padding: 30px 73px 0;
        grid-template-columns: 363px calc(100% - 363px - 21px - 21px)
    }
}

@media (max-width:992px) {
    .footer__logo--img {
        width: 55px;
        height: 55px
    }
}

@media (max-width:991px) {
    .visible-on-desktop {
        display: none!important
    }
}

@media screen and (-ms-high-contrast:none) {
    .splide.is-focus-in .splide__slide:focus,.splide__slide:focus-visible {
        border: 3px solid #0bf
    }

    .splide.is-focus-in .splide__track>.splide__list>.splide__slide:focus {
        border-color: #0bf
    }
}

@media (hover:hover) {
    .f-button:hover:not([disabled]) {
        color: var(--f-button-hover-color);
        background-color: var(--f-button-hover-bg)
    }
}

@media (any-hover:hover) {
    .arrow-btn:hover {
        box-shadow: 0 4px 20px rgba(251,59,83,.25)
    }

    .arrow-btn:active {
        background: var(--accent-active);
        box-shadow: none
    }

    .btn._transparent {
        transition: background .3s ease,color .3s ease,border-color .3s ease,box-shadow .3s ease
    }

    .btn._transparent:hover {
        background: var(--accent);
        color: var(--white)
    }

    .btn._transparent:active {
        background: var(--accent-active);
        border-color: var(--accent-active);
        color: var(--white)
    }

    .btn._min-2._transparent:hover {
        background: var(--accent);
        color: var(--white)
    }

    .btn._min-2._transparent:active {
        background: var(--accent-active);
        color: var(--white)
    }

    .btn:hover {
        box-shadow: 0 10px 25px rgba(228,5,33,.35)
    }

    .btn:active {
        background: var(--accent-active);
        box-shadow: none
    }

    .circle-btn:not(._accent):hover {
        color: var(--white);
        background: var(--accent);
        box-shadow: 0 4px 20px rgba(251,59,83,.25)
    }

    .circle-btn._accent:active,.circle-btn:not(._accent):active {
        background: var(--accent-active);
        border-color: var(--accent-active)
    }

    .circle-btn:not(._accent):active {
        color: var(--white);
        box-shadow: none
    }

    .circle-btn._accent:hover {
        box-shadow: 0 4px 20px rgba(251,59,83,.25)
    }

    .circle-btn._accent:active {
        box-shadow: none
    }

    .close-btn:active,.close-btn:hover {
        background: #dedede;
        color: #535353
    }

    .header__nav--list>li>a:hover {
        color: var(--accent)
    }

    .header__nav--list>li>a:hover::before {
        -webkit-transform: translate3d(0,0,0);
        transform: translate3d(0,0,0)
    }

    .header__nav--list>li>a:active {
        color: var(--accent-active)
    }

    .footer__contacts a:hover span,.footer__nav a:hover {
        color: var(--accent)
    }

    .header__nav--list>li>a:active::before {
        -webkit-transform: translate3d(0,50%,0);
        transform: translate3d(0,50%,0)
    }

    .header__tel:hover .header__tel--target {
        -webkit-filter: drop-shadow(0 10px 30px rgba(64, 92, 186, .5));
        filter: drop-shadow(0 10px 30px rgba(64, 92, 186, .5))
    }

    .header__tel:hover .header__tel--target svg:last-child {
        -webkit-transform: scaleY(-1);
        transform: scaleY(-1)
    }

    .header__tel:active .header__tel--target {
        -webkit-filter: drop-shadow(0 0 30px rgba(64, 92, 186, .5));
        filter: drop-shadow(0 0 30px rgba(64, 92, 186, .5))
    }

    .header__tel--target {
        transition: -webkit-filter .3s ease;
        transition: filter .3s ease;
        transition: filter .3s ease,-webkit-filter .3s ease
    }

    .footer__contacts a,.footer__nav a {
        transition: color .15s ease
    }

    .header__tel--target:hover {
        -webkit-filter: drop-shadow(0 10px 30px rgba(64, 92, 186, .5));
        filter: drop-shadow(0 10px 30px rgba(64, 92, 186, .5))
    }

    .header__tel--target:hover svg:last-child {
        -webkit-transform: scaleY(-1);
        transform: scaleY(-1)
    }

    .header__tel--target:active {
        -webkit-filter: drop-shadow(0 0 30px rgba(64, 92, 186, .3));
        filter: drop-shadow(0 0 30px rgba(64, 92, 186, .3))
    }

    .header__tel--block:hover,.header__tel--target:hover+* {
        opacity: 1;
        visibility: visible
    }

    .footer__add--block ul li {
        transition: box-shadow .3s ease
    }

    .footer__add--block ul li:hover {
        box-shadow: 0 4px 10px 0 rgba(200,200,200,.25)
    }
}

@media (min-height:700px) {
    .final-test__container {
        margin-top: 150px
    }
}






.our-teachers__slider{
    position: relative;
}

.our-teachers__nav{
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
}

.reviews__slider{
    position: relative;
}

.reviews__arrows{
    position: absolute;
    transform: translateY(-50%);
    display: flex;
    top: 50%;
    width: 100%;
    justify-content: space-between;
    margin-left: -100px;
}

.related-articles__slider{
    position: relative;
}
